import React,{Component} from 'react';
import './Header.css';
import moment from 'moment';
import Newheader from './Newheader';

const contUrl = "https://kinzoinstitute.onrender.com/contactUs"

class Contact extends Component {
    constructor(props) {
        super(props);
        this.state={
            name:'',
            email:'',
            phone:'',
            Subject:'',
            Message:'',
            date:new Date(),
           

        };
        
    }

    
    handleChange = (event) => {
        this.setState({[event.target.name]:event.target.value})
    }

    async handleSubmit() {
        
        try {

            var events = {
                email:`${this.state.email}`,
                phone:`${this.state.phone}`,
                Subject:`${this.state.Subject}`,
                Message: `${this.state.Message}`,
                date: moment(this.state.date).format('MMM-DD-YYYY')
               
            }

            let result = await fetch(contUrl, {
                method: 'post',
                
                headers: {
                    'Accept': 'application/json',
                    'Content-type': 'application/json',
                },

                body: JSON.stringify(
                    events
                )
            });
            
            console.log('result>  ' + result)
            alert("Message Submitted Successfully")
            this.setState({
                name:'',
                email:'',
                phone:'',
                Subject:'',
                Message:''
                
            })
            
            
        } catch(e) {
            console.log(e)
        }
    }

    render(){                                                                                
        return (
            <>
                 <Newheader/>
                <div className='pageSetting'>
                    
                    <div className='container '>
                        
                        <div className='row'>
                            <div className='col-5'>
                                
                                <div className="card adjustcard mobileCardd">
                                    <div className="card-img-top colorofcard" >
                                        <center><img src="https://i.imgur.com/pJMQ4rC.png" className='adjustlog' alt="logo"/></center>
                                    </div>
                                    <div className="card-body mobileBody">
                                        <h5 className="card-title">January Cohort</h5><br/>
                                        <p className="card-text">Register now to secure a seat in our next cohort coming up in January. Limited seats available</p><br/>
                                        <span><button className="btn btn-primary"> Register </button></span>
                                        <span> <img src="https://i.imgur.com/2bDfnLz.png" className='adjustinsta' alt="insta"/> </span>
                                        <p className='adjustInstTxt'>@Kinzotraininginstitute</p>
                                    </div>
                                    
                                </div>
                                
                            </div>

                            <div className ="col-5 mobileCont">
                                <center><h4>Contact Us</h4></center><br/>

                                <input type="text" className="form-control mb-3" name="name" require placeholder= "Enter Names" value={this.state.name} onChange={this.handleChange}/>
                                <input type="email" className="form-control mb-3" name="email" require placeholder= "Enter Email" value={this.state.email} onChange={this.handleChange}/>
                                <input type="number" className="form-control mb-3" name="phone" require placeholder= "Phone Number" value={this.state.phone} onChange={this.handleChange}/>
                                <input type="text" className="form-control mb-3" name="Subject" require placeholder= "Enter Subject" value={this.state.Subject} onChange={this.handleChange}/>
                                <textarea className="form-control mb-3" rows="4" name="Message" require placeholder= "Enter Message" value={this.state.Message} onChange={this.handleChange}/>
                                <br/>
                                <center><button disabled={this.state.name ===''||this.state.email===''||this.state.Message===''}type="button" class="btn btn-secondary" onClick={ () => this.handleSubmit() }>Submit</button></center>
                            </div>
                            
                            
                        </div><br/><br/>
                        

                        <center className = "mobileNumbr">
                            <p className="formttxt4">10451 Mill Run Circle, Suites 1010, Owings Mills MD 21117</p>
                            <p className="formttxt5 con2">+1 (443) 955 9906</p>
                        </center>
                                        
                       
                    </div>
                </div>
            
            </>
        )
    }

}
export default Contact;